<template>
  <div>
    <core-progress-modal :show="loading" />
    <v-tabs
      v-if="!loading"
      centered
      color="primary"
      dark
      icons-and-text
    >
      <v-tabs-slider color="white" />

      <v-tab href="#dados-cliente">
        Dados Cliente
        <v-icon>mdi-card-account-details</v-icon>
      </v-tab>

      <v-tab href="#proposta-adesao">
        Proposta de Adesão
        <v-icon>mdi-text-box-search</v-icon>
      </v-tab>

      <v-tab href="#doc-digitalizados">
        Doc. Digitalizados
        <v-icon>mdi-file-document</v-icon>
      </v-tab>

      <v-tab href="#carnes">
        Carnês
        <v-icon>mdi-animation</v-icon>
      </v-tab>

      <v-tab href="#pagamentos">
        Pagamentos
        <v-icon>mdi-cash-multiple</v-icon>
      </v-tab>

      <v-tab
        v-if="$root.isRolesPratico()"
        href="#spc">
        SPC
        <v-icon>mdi-account-off-outline</v-icon>
      </v-tab>

      <v-tab
        v-if="$root.isRolesPratico()"
        href="#renegociar">
        Renegociar Dívida
        <v-icon>mdi-handshake</v-icon>
      </v-tab>

      <v-tab-item
        value="dados-cliente"
        lazy
      >
        <cliente-dados
          :value="form"
          :next-step="salvar"
          :go-back="cancelar"
          :set-data="setData"
          :editando="true"
        />
      </v-tab-item>
      <v-tab-item
        value="proposta-adesao"
        lazy
      >
        <cliente-contrato
          :value="form"
          :editando="true"
        />
      </v-tab-item>
      <v-tab-item
        value="doc-digitalizados"
        lazy
      >
        <cliente-digitalizar-docs
          :value="form"
          :next-step="salvar"
          :go-back="cancelar"
          :editando="true"
        />
      </v-tab-item>
      <v-tab-item
        value="carnes"
        lazy
      >
        <cliente-carnes
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        value="pagamentos"
        lazy
      >
        <cliente-pagamentos
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        v-if="$root.isRolesPratico()"
        value="spc"
        lazy
      >
        <cliente-SPC
          :value="form"
        />
      </v-tab-item>
      <v-tab-item
        v-if="$root.isRolesPratico()"
        value="renegociar"
        lazy
      >
        <cliente-renegociar
          :value="form"
        />
      </v-tab-item>
    </v-tabs>
  </div>
</template>
<script>
import Endereco from '../../beans/Endereco'
import { ClienteBusiness, SPCBusiness } from '../../business'

const ClienteSPC = () => import('../clientes/ClienteSPC')
const ClienteRenegociar = () => import('../clientes/ClienteRenegociar')
const ClienteDados = () => import('./ClienteDados.vue')
const ClienteConsultaCPF = () => import('./ClienteConsultaCPF.vue')
const ClienteContrato = () => import('./ClienteContrato.vue')
const ClienteDigitalizarDocs = () => import('./ClienteDigitalizarDocs.vue')
const ClienteVender = () => import('./ClienteVender.vue')
const ClienteImprimirCarne = () => import('./ClienteImprimirCarne')
const ClienteCarnes = () => import('./ClienteCarnes.vue')
const ClientePagamentos = () => import('./ClientePagamentos')

export default {
  components: {
    ClienteSPC,
    ClienteRenegociar,
    ClienteDados,
    ClienteConsultaCPF,
    ClienteContrato,
    ClienteDigitalizarDocs,
    ClienteVender,
    ClienteImprimirCarne,
    ClienteCarnes,
    ClientePagamentos
  },
  data () {
    return {
      form: {
        id: null,
        redeId: null,
        empresaId: null,
        lojaId: null,
        nome: null,
        cpf: null,
        identidade: null,
        orgaoEmissor: null,
        dataEmissao: null,
        uf: null,
        grupo: null,
        dataNascimento: null,
        naturalEstado: null,
        naturalCidade: null,
        sexo: null,
        estadoCivil: null,
        dependentes: null,
        nomePai: null,
        nomeMae: null,
        telResidencial: null,
        celular: null,
        email: null,
        escolaridade: null,
        rendaPrincipal: null,
        endereco: new Endereco(),
        tipoResidencia: null,
        tempoResidenciaAnos: null,
        tempoResidenciaMeses: null,
        empresa: null,
        cargo: null,
        tempoEmpregoAnos: null,
        tempoEmpregoMeses: null,
        telComercial: null,
        ramal: null,
        nomeRef1: null,
        relacaoRef1: null,
        telRef1: null,
        nomeRef2: null,
        relacaoRef2: null,
        telRef2: null,
        nomeRef3: null,
        relacaoRef3: null,
        telRef3: null,
        nomeRef4: null,
        relacaoRef4: null,
        telRef4: null,
        vendaId: null,
        observacoes: [],
        ultimoHistoricoSPC: null
      },
      loading: true
    }
  },
  beforeMount () {
    this.reload(this.$route.params.id, this.$route.params.lojaId)
  },
  methods: {
    reload (clienteId, lojaId) {
      this.form.id = clienteId
      this.form.lojaId = lojaId
      // eslint-disable-next-line no-extra-boolean-cast
      if (!!this.form.id) {
        ClienteBusiness.getById(this.form.id).then(response => {
          this.form = response.data
          this.form.lojaId = this.$route.params.lojaId
          this.formatDatas()
        }).finally(() => {
          if (this.form.status === 'BLOQUEADO') {
            SPCBusiness.findUltimoHistoricoClienteSPC(this.form.id)
              .then(response => {
                this.form.ultimoHistoricoSPC = response.data
              }).finally(() => {
                this.loading = false
              })
          } else {
            this.loading = false
          }
        })
      }
    },
    salvar () {
      this.$root.showSucesso('Dados atualizados com sucesso')
    },
    setData (data) {
      let lojaId = this.form.lojaId
      this.form = data
      if (lojaId) {
        this.form.lojaId = lojaId
      }
      this.formatDatas()
    },
    formatDatas () {
      if (this.form.dataEmissao) {
        this.form.dataEmissao = this.$moment(this.form.dataEmissao).format('YYYY-MM-DD')
      }
      if (this.form.dataNascimento) {
        this.form.dataNascimento = this.$moment(this.form.dataNascimento).format('YYYY-MM-DD')
      }
      if (this.form.observacoes) {
        this.form.observacoes = this.form.observacoes.map(value => {
          value.dataCadastro = this.$moment(value.dataCadastro).format('YYYY-MM-DD')
          return value
        })
      }
    },
    cancelar () {
      this.$router.push(`/lista-cliente`)
    }
  }
}
</script>
